<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        <v-row align="center" justify="center">
          <v-btn-toggle dense borderless background-color="white accent-3">
            <v-btn @click="undo">
              <!-- 撤销 -->
              <v-icon dark>mdi-undo</v-icon>
            </v-btn>

            <v-btn @click="redo">
              <!-- 重做 -->
              <v-icon dark>mdi-redo</v-icon>
            </v-btn>

            <v-btn @click="resetToOrigin">
              <!-- 恢复原图 -->
              <v-icon dark>mdi-restore</v-icon>
            </v-btn>
          </v-btn-toggle>
        </v-row>
      </v-col>
    </v-row>

    <!-- 历史记录 -->
    <v-subheader>历史记录</v-subheader>
    <v-list shaped>
      <v-list-item-group v-model="curPointer" color="primary">
        <v-list-item
          v-for="(item, i) in this.opList.items"
          :key="i"
          @click="setTo(i)"
        >
          <v-list-item-content>
            <v-list-item-title v-text="item.name"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-container>
</template>

<script>
import { resetWithImgData } from "../js/operator";
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';

export default {
  name: "rightSidebar",
  data() {
	return {
		curPointer: 0,	// 标记当前历史记录高亮位置
		
		// 需要检查的，可能与当前操作发生冲突的状态列表
		opCheckList: [
			{'op': this.getClipState, 'name': '裁剪', 'todo': this.endClip, 'param': 0},
			{'op': this.getScaleState, 'name': '缩放', 'todo': this.endScaling, 'param': 0},
			{'op': this.getRotateState, 'name': '旋转', 'todo': this.endRotating, 'param': 0}
		]
	}
  },
  watch: {
	listenList: {
		// 监听列表变化，更新高亮条目
		handler(curVal) {
			this.curPointer = curVal;
		}
	}
  },
  computed: {
	...mapState([	// 全局变量
		'opList', 'canvas', 'cxt', 'originImgInfo',
	]),
	listenList () {
		return this.opList.getCurPtr();
	}
  },
  methods: {
	// ========================== 全局方法 ==========================
	...mapGetters('clip', [ 'getClipState']),
	...mapGetters('scale', [ 'getScaleState']),
	...mapGetters('rotate', [ 'getRotateState']),
	
	...mapActions('clip', ['endClip']),
	...mapActions('scale', ['endScaling']),
	...mapActions('rotate', ['endRotating']),
	
	...mapMutations(['setToOrigin']),
	// ========================== 特殊处理 ==========================
	preCheck() {
		/* 避免冲突，预先判断系统是否正在执行其它用例 */
		for(var j = 0,len = this.opCheckList.length; j < len; j++){
			var e = this.opCheckList[j];
			if (e.op()) {	// 发现冲突，系统发出风险提示
				var msg = e.name + "结果尚未保存，是否放弃？";
				if (confirm(msg)) e.todo(e.param);
				else return false;
			}
		}
		// 未发现冲突，通过检测，返回 true，
		return true;
	},
	// ========================== 功能实现 ==========================
	resetToOrigin() {
		/* 重置 */
		if (confirm("您点击了恢复原图，该操作不可撤销，是否确认？")) {
			this.setToOrigin();
			this.opList.clear();
			this.opList.push({ data: this.originImgInfo, name: "原图" });
			console.log("opList:", this.opList.size());
		}
	},
    undo() {
		/* 撤销 */
		if(!this.preCheck()) return;
		if (this.opList.getCurPtr() == 0) {
			alert("没有可以撤销的操作了");
			return;
		}
		console.log("undo canvas ", this.canvas);
		const now = this.opList.getNowAndForward();
		resetWithImgData(this.canvas, now.data);
		console.log("opList:", this.opList.size());
    },
	redo() {
		/* 重做 */
		if(!this.preCheck()) return;
		if (this.opList.getCurPtr() == this.opList.size() - 1) {
			alert("没有可以重做的操作了");
			return;
		}
		const now = this.opList.getNowAndBackward();
		const imgData = now.data;
		resetWithImgData(this.canvas, imgData);
		console.log("opList:", this.opList.size());
	},
	setTo(i) {
		/* 恢复到指定历史状态 */
		if(!this.preCheck()) return;
		const now = this.opList.getAndSetNow(i);
		if (now == undefined) {
			console.log("非法操作");
			return;
		}
		console.log("history i: ", i);
		const imgData = now.data;
		resetWithImgData(this.canvas, imgData);
	}
  }
};
</script>