import { parsing } from "../../js/operator";
const rotateModule = {
	namespaced: true,
	state: {
		rotateState: false,  // 标记是否处于旋转状态
		theta: 0,		// 旋转角度
		curImg: null,	// 记录操作开始时的图片数据
		imgW: 0,		// 图片宽度
		imgH: 0,		// 图片高度
		funcId: null,	// 记录操作编号
	},
	getters: { // 获取数据本身的属性
		getRotateState (state) {return state.rotateState;}
	},
	mutations: { // commit 修改数据
		setTheta(state, e) {
			state.theta = e;
		}
	},
	actions: {
		setImgByCanvas ({state, rootState}) {
			// 记录初始图片信息
			return new Promise((resolve) => {
				state.imgW = rootState.canvas.width
				state.imgH = rootState.canvas.height
				state.curImg = new Image();
				state.curImg.crossOrigin = '';
				state.curImg.src = rootState.canvas.toDataURL();
				// 兼容性图片onload事件
				state.curImg.onload = function(){
					resolve();
				}
				if (state.curImg.complete) {
					resolve();	// 跳过onload直接返回
				}
			});
		},
		async initRotation ({state, rootState, dispatch}, n) {
			console.log('旋转初始化')
			state.funcId = n
			rootState.showLowerSide = false		// 隐藏主界面下方 "选取/保存图片组件"
			await dispatch('setImgByCanvas')	// 记录初始图片信息
			state.theta = 0				// 初始旋转角度置0
			state.rotateState = true	// 标记处于旋转状态
		},
		reRotate({state, rootState}) {
			// 用户拖动控制条触发
			if(!state.rotateState) return;
			console.log('重新旋转')
			rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height)
			
			rootState.cxt.save()
			rootState.cxt.translate(rootState.canvas.width/2, rootState.canvas.height/2)
			rootState.cxt.rotate(state.theta*Math.PI/180);
			rootState.cxt.drawImage(state.curImg, -state.imgW/2, -state.imgH/2)
			rootState.cxt.restore()
		},
		async endRotating({rootState, dispatch}, e) {
			await dispatch('exit', e)
			console.log('退出旋转')
			rootState.showLowerSide = true	// 显示主界面下方 "选取/保存图片组件"
		},
		exit ({state, rootState}, e) {
			if (e == 0) { // 取消旋转，恢复初始状态
				console.log('取消旋转', e)
				rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height)
				rootState.canvas.width = state.imgW
				rootState.canvas.height = state.imgH
				rootState.cxt.drawImage(state.curImg, 0, 0)
				state.rotateState = false
			} else { // 确认旋转，更新画布与历史记录列表
				console.log('确认旋转', e)
				// 更新历史记录列表
				return new Promise((resolve) => {
					rootState.opList.removeBackPart();
					rootState.opList.push(parsing(rootState.canvas, state.funcId));
					console.log('历史记录已更新');
					state.rotateState = false
					resolve();
				});
			}
		}
	}
}

export default rotateModule;