import { parsing } from "../../js/operator";
const clipModule = {
	namespaced: true,
	state: {
		clipState: false,	// 标记是否处于裁剪状态
		afterValidClick: false, // 标记是否点击了截图框区域
		funcId: null,	// 记录操作编号
		boxParams: {	// 裁剪框相关参数
			X: null,
			Y: null,
			W: null,
			H: null,
			currentX: null,
			currentY: null,
			kind: null,
		},
		boxStyle: {	// 裁剪框样式
			position:'absolute',
			border:'1px solid black',
			height: '0',
			width: '0',
			left: '0',
			top: '0',
		},
	},
	getters: { // 获取数据本身的属性
		getClipState (state) {return state.clipState;}
	},
	mutations: { // commit 修改数据
		mouseDownEvent (state, e) {  // 鼠标按下时触发的事件
			if (!state.clipState || (e.target.parentNode.id !== "box" && e.target.id !== "box")) return;
			console.log('点击落在裁剪框内', e.target.parentNode.id, e.target.id, state.boxParams);
			state.afterValidClick = true	// 将鼠标在裁剪框内点击视为合法点击，指示之后需要追踪鼠标的移动情况
			state.boxParams.currentX = e.clientX  // 鼠标按下时坐标x轴
			state.boxParams.currentY = e.clientY  // 鼠标按下时坐标y轴
			state.boxParams.kind = e.target.id	// 记录鼠标按下的区域
		},
		setBoxParams (state, e) {
			state.boxParams.X = e.X
			state.boxParams.Y = e.Y
			state.boxParams.W = e.W
			state.boxParams.H = e.H
		},
		resetAll (state) {
			state.afterValidClick = false
			state.boxParams.X = null
			state.boxParams.Y = null
			state.boxParams.W = null
			state.boxParams.H = null
			state.boxParams.currentX = null
			state.boxParams.currentY = null
			state.boxParams.kind = null
		}
	},
	actions: {
		initClip ({dispatch, state, rootState, commit}, n) {
			console.log('裁剪初始化')
			state.funcId = n
			state.clipState = true	// 标记当前正处于裁剪状态
			rootState.showLowerSide = false	// 隐藏主界面下方 "选取/保存图片组件"
			commit('resetAll')	// 重置裁剪框所有参数
			
			// 将蒙层设置为图片大小
			rootState.clipMask.width = rootState.canvas.width
			rootState.clipMask.height = rootState.canvas.height
			rootState.clipFrame.style.width = rootState.canvas.width + "px"
			rootState.clipFrame.style.height = rootState.canvas.height + "px"
			console.log(rootState.clipFrame)
			
			// 将截图框初始化为与图片短边等长的正方形
			var initBoxSize = Math.min(rootState.canvas.width, rootState.canvas.height)
			var x = (rootState.canvas.width - initBoxSize) / 2
			var y = (rootState.canvas.height - initBoxSize) / 2
			commit('setBoxParams', {
				X: x,
				Y: y,
				W: initBoxSize,
				H: initBoxSize
			})
			state.boxStyle.height = state.boxStyle.width = initBoxSize + "px"
			state.boxStyle.left = x + "px"
			state.boxStyle.top = y + "px"
			dispatch('drawTrim')
		},
		mouseMoveEvent ({ dispatch, state, rootState}, e) {  // 鼠标移动事件
			if (!state.afterValidClick || !state.clipState) return;
			console.log('mouseMoveEvent', e)
					
			var nowX = e.clientX; // 鼠标移动时x坐标
			var nowY = e.clientY; // 鼠标移动时y坐标
			var disX = nowX - state.boxParams.currentX;  // 鼠标x方向移动距离
			var disY = nowY - state.boxParams.currentY;  // 鼠标y方向移动距离
			
			// ====================== 控制裁剪框随鼠标变换 ===========================
			var calLeft = state.boxParams.X
			var calTop = state.boxParams.Y
			var calW = state.boxParams.W
			var calH = state.boxParams.H
			var rangeX = [0, rootState.canvas.width]
			var rangeY = [0, rootState.canvas.height]
			var rangeW = [0, rootState.canvas.width]
			var rangeH = [0, rootState.canvas.height]
			if (state.boxParams.kind === "UC") {	//上拉伸
				calTop += disY;
				calH -= disY;
				rangeY[1] = rangeH[1] = state.boxParams.H + state.boxParams.Y
			} else if (state.boxParams.kind === "LC") { //左拉伸
				calLeft += disX;
				calW -= disX;
				rangeX[1] = rangeW[1] = state.boxParams.W + state.boxParams.X
			} else if (state.boxParams.kind === "RC") { //右拉伸
				calW += disX;
				rangeX[0] = state.boxParams.X
				rangeW[1] -= state.boxParams.X
			} else if (state.boxParams.kind === "DC") { //下拉伸
				calH += disY;
				rangeY[0] = state.boxParams.Y
				rangeH[1] -= state.boxParams.Y
			} else if (state.boxParams.kind === "LU") { //左上拉伸
				calLeft += disX;
				calW -= disX;
				calTop += disY;
				calH -= disY;
				rangeX[1] = rangeW[1] = state.boxParams.W + state.boxParams.X
				rangeY[1] = rangeH[1] = state.boxParams.H + state.boxParams.Y
			} else if (state.boxParams.kind === "RU") { //右上拉伸
				calTop += disY;
				calH -= disY;
				calW += disX;
				rangeX[0] = state.boxParams.X
				rangeW[1] -= state.boxParams.X
				rangeY[1] = rangeH[1] = state.boxParams.H + state.boxParams.Y
			} else if (state.boxParams.kind === "LD") { //左下拉伸
				calLeft += disX;
				calW -= disX;
				calH += disY;
				rangeX[1] = rangeW[1] = state.boxParams.W + state.boxParams.X
				rangeY[0] = state.boxParams.Y
				rangeH[1] -= state.boxParams.Y
			} else if (state.boxParams.kind === "RD") { //右下拉伸
				calW += disX;
				calH += disY;
				rangeX[0] = state.boxParams.X
				rangeW[1] -= state.boxParams.X
				rangeY[0] = state.boxParams.Y
				rangeH[1] -= state.boxParams.Y
			} else { //移动
				calLeft += disX;
				calTop += disY;
				rangeX[1] -= state.boxParams.W
				rangeY[1] -= state.boxParams.H
			}
			state.boxStyle.top = Math.max(rangeY[0], Math.min(rangeY[1], calTop)) + "px"
			state.boxStyle.left = Math.max(rangeX[0], Math.min(rangeX[1], calLeft)) + "px"
			state.boxStyle.height = Math.max(rangeH[0], Math.min(rangeH[1], calH)) + "px"
			state.boxStyle.width = Math.max(rangeW[0], Math.min(rangeW[1], calW)) + "px"
			console.log(state.boxParams.kind, state.boxStyle.left, state.boxStyle.top, state.boxStyle.width, state.boxStyle.height)
			dispatch('drawTrim');
		},
		mouseReleaseEvent ({state, commit}) {  // 鼠标抬起事件
			if (!state.afterValidClick || !state.clipState) return;
			state.afterValidClick = false	// 标记为false，不再追踪鼠标移动情况
			
			// 保存相关裁剪选择框信息
			commit('setBoxParams', {
				X: parseInt(state.boxStyle.left),
				Y: parseInt(state.boxStyle.top),
				W: parseInt(state.boxStyle.width),
				H: parseInt(state.boxStyle.height)
			})
		},
		drawTrim ({state, rootState}) {  // 绘制裁剪框
			console.log('drawTrim')
			// 每帧都要清除画布
			rootState.maskCxt.clearRect(0, 0, rootState.clipMask.width, rootState.clipMask.height);
			
			// 绘制蒙层
			rootState.maskCxt.save();
			rootState.maskCxt.fillStyle = 'rgba(166, 166, 166, 0.6)';
			rootState.maskCxt.fillRect(0, 0, rootState.clipMask.width, rootState.clipMask.height);
			
			// 将蒙层凿开，凸显当前裁剪区域
			rootState.maskCxt.globalCompositeOperation = 'source-atop';
			rootState.maskCxt.clearRect(parseInt(state.boxStyle.left), parseInt(state.boxStyle.top), 
										parseInt(state.boxStyle.width), parseInt(state.boxStyle.height)); // 裁剪选择框
			rootState.maskCxt.restore();
		},
		async endClip ({rootState, dispatch}, e) {
			await dispatch('exit', e)
			console.log('退出裁剪')
			rootState.showLowerSide = true	// 显示主界面下方 "选取/保存图片组件"
		},
		exit({state, rootState, commit}, e){
			if(e == 0){
				// 取消裁剪，画布状态不改变
				rootState.maskCxt.clearRect(0, 0, rootState.clipMask.width, rootState.clipMask.height);
				commit('resetAll')
				state.clipState = false
			} else {
				// 确认裁剪，获取裁剪框下方图片数据
				var data = rootState.cxt.getImageData(state.boxParams.X, state.boxParams.Y,
														state.boxParams.W, state.boxParams.H);
				console.log(data)
				// 更新画布
				rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height);
				rootState.maskCxt.clearRect(0, 0, rootState.clipMask.width, rootState.clipMask.height);
				rootState.canvas.width = state.boxParams.W;
				rootState.canvas.height = state.boxParams.H;
				rootState.cxt.putImageData(data, 0, 0);
				
				// 裁剪状态置为false
				state.clipState = false;
				console.log('裁剪完成');
				
				// 更新历史记录列表
				return new Promise((resolve) => {
					rootState.opList.removeBackPart();
					rootState.opList.push(parsing(rootState.canvas, state.funcId));
					console.log('历史记录已更新');
					resolve();
				});
			}
		}
	}
}

export default clipModule;