import { parsing } from "../../js/operator";
const scaleModule = {
	namespaced: true,
	state: {
		scaleState: false,  // 标记是否处于缩放状态
		scaleRatio: 100,	// 当前缩放比例
		curImg: null,	// 记录操作开始时的图片数据
		imgW: 0,		// 图片宽度
		imgH: 0,		// 图片高度
		max: null,		// 最大放大比例
		funcId: null,	// 记录操作编号
	},
	getters: { // 获取数据本身的属性
		getScaleState (state) {return state.scaleState;}
	},
	mutations: { // commit 修改数据
		setScaleRatio(state, e) {
			state.scaleRatio = e;
		}
	},
	actions: {
		setImgByCanvas ({state, rootState}) {
			// 记录初始图片信息
			return new Promise((resolve) => {
				state.imgW = rootState.canvas.width
				state.imgH = rootState.canvas.height
				state.curImg = new Image();
				state.curImg.crossOrigin = '';
				state.curImg.src = rootState.canvas.toDataURL();
				// 兼容性图片onload事件
				state.curImg.onload = function(){
					resolve();
				}
				if (state.curImg.complete) {
					resolve();	// 跳过onload直接返回
				}
			});
		},
		async initScaling ({state, rootState, dispatch}, n) {
			console.log('缩放初始化')
			state.funcId = n
			rootState.showLowerSide = false		// 隐藏主界面下方 "选取/保存图片组件"
			await dispatch('setImgByCanvas')	// 记录初始图片信息
			state.max = rootState.edgeH*200 / state.imgH	// 设置最大放大比例
			state.scaleRatio = 100		// 初始化缩放比例
			
			// 将画布区域设置到最大
			rootState.canvas.width = rootState.edgeW
			rootState.canvas.height = rootState.edgeH
			// 重设后canvas会重新加载，故要重新画图
			rootState.cxt.drawImage(state.curImg, (rootState.canvas.width - state.imgW)/2, (rootState.canvas.height - state.imgH)/2)
			state.scaleState = true	// 标记处于缩放状态
		},
		reScale({state, rootState}) {
			// 用户拖动控制条触发
			if(!state.scaleState) return;
			console.log('重新缩放')
			var rate = state.scaleRatio / 100
			rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height)
			
			rootState.cxt.save()
			rootState.cxt.translate(rootState.canvas.width/2, rootState.canvas.height/2)
			rootState.cxt.scale(rate, rate)
			rootState.cxt.drawImage(state.curImg, -state.imgW/2, -state.imgH/2)
			rootState.cxt.restore()
		},
		async endScaling({rootState, dispatch}, e) {
			await dispatch('exit', e)
			console.log('退出缩放')
			rootState.showLowerSide = true	// 显示主界面下方 "选取/保存图片组件"
		},
		exit({state, rootState}, e) {
			if (e == 0) { // 取消缩放，恢复初始状态
				console.log('取消缩放', e)
				rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height)
				rootState.canvas.width = state.imgW
				rootState.canvas.height = state.imgH
				rootState.cxt.drawImage(state.curImg, 0, 0)
				state.scaleState = false
			} else { // 确认缩放，更新画布与历史记录列表
				console.log('确认缩放', e)
				var rate = state.scaleRatio / 100
				rootState.cxt.clearRect(0, 0, rootState.canvas.width, rootState.canvas.height)
				
				var sx = 0, sy = 0, sw, sh, dw = state.imgW * rate, dh = state.imgH * rate
				console.log('test:',dw,dh)
				// 画布更新为缩放后画布大小，并重新绘制图片
				// 注意 sx,sy,sw,sh 是相对于 source_img 而言，不是 canvas
				if(dw <= rootState.edgeW && dh <= rootState.edgeH){	// 放大比率不超过边界时
					rootState.canvas.width = dw
					rootState.canvas.height = dh
					sw = state.imgW
					sh = state.imgH
				} else {  // 放大比率超过边界时
					sx = Math.max((dw - rootState.edgeW) / (2 * rate), 0)
					sy = Math.max((dh - rootState.edgeH) / (2 * rate), 0)
					sw = Math.min(rootState.edgeW, dw) / rate
					sh = Math.min(rootState.edgeH, dh) / rate
					rootState.canvas.width = Math.min(rootState.edgeW, dw)
					rootState.canvas.height = Math.min(rootState.edgeH, dh)
				}
				rootState.cxt.drawImage(state.curImg, sx, sy, sw, sh, 0, 0, rootState.canvas.width, rootState.canvas.height)
				
				// 更新历史记录列表
				return new Promise((resolve) => {
					rootState.opList.removeBackPart();
					rootState.opList.push(parsing(rootState.canvas, state.funcId));
					console.log('历史记录已更新');
					state.scaleState = false
					resolve();
				});
			}
		}
	}
}

export default scaleModule;