<template>
	<v-container fluid
		@mousedown="mouseDownEvent($event)"
		@mousemove="mouseMoveEvent($event)"
		@mouseup="mouseReleaseEvent($event)"
	>
	<v-row>
		<v-col cols="12">
			<!-- 缩放控制条，处于缩放状态时显示，控制缩放倍数 -->
			<v-row v-show="scaleState" align= "stretch" justify="center">
				<v-col cols="6">
					<v-slider
						v-model="editScale"
						class="align-center"
						:max="max" min="0"
						thumb-label="always"
					>
						<template v-slot:thumb-label="{ value }">
							{{ value/100 }}x
						</template>
					</v-slider>
				</v-col>
				<v-btn v-on:click="endScaling(1)" class="mx-2" outlined fab small color="indigo">
					<v-icon>mdi-check</v-icon>
				</v-btn>
				<v-btn v-on:click="endScaling(0)" class="mx-2" outlined fab small color="indigo">
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-row>
			
			<!-- 旋转控制条，处于旋转状态时显示，控制旋转角度 -->
			<v-row v-show="rotateState" align= "stretch" justify="center">
				<v-col cols="6">
					<v-slider
						v-model="editRotate"
						class="align-left"
						:max="360" min="0"
						thumb-label="always"
					>
						<template v-slot:thumb-label="{ value }">
							{{ value }}°
						</template>
					</v-slider>
				</v-col>
				<v-btn v-on:click="endRotating(1)" class="mx-2" outlined fab small color="indigo">
					<v-icon>mdi-check</v-icon>
				</v-btn>
				<v-btn v-on:click="endRotating(0)" class="mx-2" outlined fab small color="indigo">
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-row>
			
			<!-- 裁剪控制，处于裁剪状态时显示，选择确认或取消 -->
			<v-row v-show="clipState" align= "stretch" justify="center">
				<v-btn v-on:click="endClip(1)" class="mx-2 my-1" outlined fab small color="indigo">
					<v-icon>mdi-check</v-icon>
				</v-btn>
				<v-btn v-on:click="endClip(0)" class="mx-2 my-1" outlined fab small color="indigo">
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-row>
			
			<!-- 画布容器，放置画布，及展示在画布区域上的组件 -->
			<v-row id="canvasWrapper" align="center" justify="center" class="grey lighten-5">
				<!-- 默认图容器，不显示 -->
				<img crossorigin="" v-show="false" id="myImg1" src="../assets/default1.png"/>
				<img crossorigin="" v-show="false" id="myImg2" src="../assets/default2.png"/>
				<img crossorigin="" v-show="false" id="myImg3" src="../assets/default3.png"/>
				<!-- 裁剪框，处于裁剪状态时显示 -->
				<div id="clipFrame" style="position: absolute; z-index: 1;" v-show="clipState">
					<div id="box" v-bind:style="[boxStyle]" style="cursor: move;">
						<!-- 八个可拖拽控制点，用于调整裁剪框大小 -->
						<div id="LU" style="left:-3px; top:-3px; cursor:nw-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="LD" style="left:-3px; bottom:-3px; cursor:sw-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="RU" style="right:-3px; top:-3px; cursor:ne-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="RD" style="right:-3px; bottom:-3px; cursor:se-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="UC" style="top:-3px; left:50%; margin-left:-3px; cursor:n-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="DC" style="bottom:-3px; left:50%; margin-left:-3px; cursor:s-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="RC" style="right:-3px; top:50%; margin-top:-3px; cursor:e-resize;" 
							v-bind:style="[dotStyle]"></div>
						<div id="LC" style="left:-3px; top:50%; margin-top:-3px; cursor:w-resize;" 
							v-bind:style="[dotStyle]"></div>
					</div>
				</div>
				
				<!-- 主画布 -->
				<canvas id="myCanvas" style="position: absolute;"></canvas>
				<!-- 裁剪蒙层，遮罩未被选取的裁剪区域，以凸显裁剪区域 -->
				<canvas id="clipMask" style="position: absolute;"></canvas>
			</v-row>
			
			<!-- 画布下方控制区域，放置选择图片与保存图片组件 -->
			<v-row id="lowerSide" align= "stretch" justify="center" v-show="showLowerSide">
				<v-col cols="5">
					<v-select label="选择图片"
						v-model="choice" v-if="hackReset"
						:items="defaultImgs" 
						item-text="name"
						item-value="value"
						outlined
						@change="showWarningMsg" 
					></v-select>
				</v-col>
				<!-- 辅助触发图片上传，不显示 -->
				<input id="uploadImg" type="file" style="display: none" v-on:change="getUserImg"/>
				
				<v-btn v-on:click="downloadImage" class="ma-4">
					保存图片
				</v-btn>
			</v-row>
		</v-col>
	</v-row>
	</v-container>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';

export default {
    name: 'midBlock',
	data() {
		return {
			// =================== '选择图片' 相关参数 ====================
			choice: 1,		// 在组件上点选的图片编号
			lastChoice: 1,	// 点击前图片编号
			confirmChange: true,	// 标记用户是否确认修改
			defaultImgs: [	// 可选项名称与对应编号
				{name: '花卉', value: 1},
				{name: '自然', value: 2},
				{name: '人像', value: 3},
				{name: '自定义', value: 0},
			],
			firstCallHandler: true, // 阻止循环触发 handler
			hackReset: true,		// 强制select组件重新渲染
			
			// ================== 裁剪框控制点共用样式 ====================
			dotStyle: {
				position:'absolute',
				width:'6px',
				height:'6px',
				border:'1px solid #000',
				background:'white',
				overflow:'hidden'
			}
		}
	},
	watch: {
		// ========================== 监听与重载 ==========================
		choice: {
			handler(curVal, oldVal) {
				/* 
					监听v-select组件的变化（选择图片）
						若确认修改图片，调用修改函数
						否则，选框重新渲染，恢复旧值
				*/
				console.log('监听到改变: ',curVal, oldVal, 
							'用户是否确认改变: ',this.confirmChange);
				this.lastChoice = oldVal;
				if (this.confirmChange)	{	// 确认修改，调用修改函数
					this.changeImg(curVal);
				}
				else if (this.firstCallHandler) {
					this.firstCallHandler = false;
					this.choice = this.lastChoice;	// 恢复旧值
					this.hackReset = false;		// 选框重新渲染
					this.$nextTick(() => {
						this.hackReset = true
					});
				} else {
					this.firstCallHandler = true;	// 状态重置
				}
			}
		},
		editScale: {
			handler() {
				/* 监听v-slider组件的变化，实现图片缩放的重新渲染 */
				this.reScale()
			}
		},
		editRotate: {
			handler() {
				/* 监听v-slider组件的变化，实现图片旋转的重新渲染 */
				this.reRotate()
			}
		}
	},
	computed: {
		// ========================== 全局变量 ==========================
		...mapState([
			'opList', 'canvas', 'cxt', 'clipMask', 'maskCxt',
			'edgeW', 'edgeH', 'showLowerSide'
		]),
		...mapState('clip', [
			'clipState', 'boxStyle', 'boxStyle',
		]),
		...mapState('scale', [
			'scaleState', 'scaleRatio', 'max'
		]),
		...mapState('rotate', [
			'rotateState', 'theta'
		]),
		// ======================= 操作状态控制 ==========================
		editScale: {
			get() {
				return this.scaleRatio;
			},
			set(e) {
				this.setScaleRatio(e);
			}
		},
		editRotate: {
			get() {
				return this.theta;
			},
			set(e) {
				this.setTheta(e);
			}
		}
	},
	// ================================= 界面初始化 =================================
	mounted: async function(){
		console.log('进入图片预设函数');
		
		// 获取界面可用大小，设置画布边界
		var canvasDiv = document.getElementById("canvasWrapper");
		var cont = document.getElementsByClassName("v-content__wrap");
		canvasDiv.style.height = (cont[0].clientHeight - 170) + "px";
		this.setEdge({'edgeH': cont[0].clientHeight - 220, 'edgeW': cont[0].clientWidth})
		
		// 将常用接口设为全局变量
		var canvas = document.getElementById("myCanvas");
		var clipMask = document.getElementById("clipMask");
		var clipFrame = document.getElementById("clipFrame");
		if(canvas.getContext) {
			console.log('浏览器支持Canvas!');
			// var cxt = canvas.getContext('2d');
			// var maskCxt = clipMask.getContext('2d');
			this.storeInterfaces({
				canvas: canvas,
				clipMask: clipMask,
				clipFrame: clipFrame
			});	
			console.log('画布与接口初始化完成');
			await this.fillCanvas(1, this.callback);  // 图片绘制到画布上
			console.log('画布准备完毕', this.canvas, this.cxt);
			this.storeOriginImgInfo(); // 保存原图数据
			console.log('初始化完成', this.opList)
		}
		else {
			alert('抱歉，您的浏览器不支持Canvas，请更新版本后重试');
		}
	},
	methods: {
		// ============================== 全局方法 ==============================
		...mapMutations([	
			'storeInterfaces', 'storeOriginImgInfo', 'setEdge'
		]),
		...mapMutations('clip',[
			'mouseDownEvent',
		]),
		...mapMutations('scale',[
			'setScaleRatio',
		]),
		...mapMutations('rotate',[
			'setTheta',
		]),
		...mapActions('clip',[
			'mouseMoveEvent',
			'mouseReleaseEvent',
			'endClip'
		]),
		...mapActions('scale',[
			'reScale', 'endScaling'
		]),
		...mapActions('rotate',[
			'reRotate', 'endRotating'
		]),
		// ============================== 图片下载  ============================== 
		downloadImage() {
			/* 功能：将图片转码为jpeg格式，并触发下载到本地 */
			console.log('图片下载到本地');
			var aTag = document.createElement('a');
			this.canvas.toBlob(function(blob) {
				aTag.download = 'imgFile.jpg';
				aTag.href = URL.createObjectURL(blob);
				aTag.click();
			}, "image/jpeg", 1.0);
		},
		// ============================= 图片上传  ============================== 
		getUserImg: async function () {
			/* 功能：
				读取用户上传的图片并转码
				将新图片更新到画布上
			*/
			var fileInput = document.getElementById("uploadImg");
			if (fileInput.files.length == 0) {
				console.log('没有可用文件');
				return;
			}
			// 等待转码结果返回
			var rawImgData = undefined;
			await this.getRawImgData(fileInput.files[0]).then(function (res) {
				rawImgData = res;
			});
			// 将新图片写入画布
			this.changeImg(rawImgData);
		},
		// ================================ 辅助函数  ==============================
		showWarningMsg () {
			/* 功能：更改图片前发出风险提示 */
			this.confirmChange = confirm("更改图片后，当前画布上的图片将会丢失，是否确认？");
		},
		getRawImgData (file) {
			/* 功能：读取用户上传的图片并转码 */
			return new Promise((resolve) => {
				var reader = new FileReader();
				// 以DataURL的形式读取文件, 发起一个异步操作来读取文件内容
				reader.readAsDataURL(file);
				reader.onload = function(e) {  // 文件读取完后调用
					resolve(e.target.result);	// 返回转码结果
				}
			});
		},
		// ========================= 根据给定数据源更新画布  =========================
		changeImg: async function (newImgSrc) {
			/*
				功能：将新图片更新到画布上，并传回给父组件
				newImgSrc:
					case1: 当用户选择上传自定义图片时，取值为 0；
					case2：当用户选择默认图时，取值为默认图编号；[1,2,3]
					case3：当用户的自定义图片转码成功后，取值为图片编码；[data:image/jpeg/...base64]
			*/
			this.opList.clear();	// 清空历史记录
			
			if (newImgSrc == 0) { // case1，点击触发input元素
				document.getElementById("uploadImg").click();
			}
			await this.fillCanvas(newImgSrc, this.callback);  // 等待图片绘制到画布上
			console.log('画布准备完毕', this.canvas, this.cxt);
			this.storeOriginImgInfo();	// 保存原图信息
		}, 
		fillCanvas (imgSrc, callback) {
			/* 辅助changeImg函数：将新图片更新到画布上 */
			return new Promise((resolve) => {
				var img = undefined;
				if (imgSrc == 1 || imgSrc == 2 || imgSrc == 3) { // 处理case2
					img = document.getElementById("myImg" + imgSrc);
				}
				else { // 处理case3
					img = new Image();
					img.crossOrigin = '';
					img.src = imgSrc;
				}
				
				// 兼容性图片onload事件
				img.onload = function(){
					console.log('调用onload');
					callback(img);
					resolve();
				}
				if (img.complete) {
					console.log('调用complete');
					callback(img);
					resolve();	// 跳过onload直接返回
				}
			});
		},
		callback (img) {
			/*
				fillCanvas的回调函数
				- 画布清空
				- 如果新图片超过屏幕大小，缩小图片
				- 将画布大小与新图片大小设为一致
				- 绘制图片
			*/
			console.log('清空画布');
			this.cxt.clearRect(0, 0, this.canvas.offsetWidth, this.canvas.offsetHeight);
			// 判断是否需要缩小图片，计算宽高比不变的前提下的缩小比率
			this.canvas.width = Math.min(this.edgeW, img.width);
			this.canvas.height = Math.min(this.edgeH, img.height);
			var xRate = this.canvas.width / img.width;
			var yRate = this.canvas.height / img.height;
			var setRate = xRate < yRate ? xRate : yRate;
			setRate = Math.min(1, setRate); // 允许缩小，不允许放大
			
			// 计算调整后的图片大小
			var dW = img.width * setRate; 
			var dH = img.height * setRate;
			
			// 设置画布大小和图片一致
			this.canvas.width = dW; 
			this.canvas.height = dH;
			
			// 绘制
			this.cxt.drawImage(img, 0, 0, dW, dH);
			console.log('画布绘制完成');
		},
	},
  }
</script>
