import Vue from 'vue'
import Vuex from 'vuex'
import clipModule from "./modules/clip.js"
import scaleModule from "./modules/scale.js"
import rotateModule from "./modules/rotate.js"
import { historyModel } from '../js/historyModel.js'

Vue.use(Vuex)

const store = new Vuex.Store({
	modules: {
		clip: clipModule,		// 裁剪
		scale: scaleModule,		// 缩放
		rotate: rotateModule,	// 旋转
	},
    state: { // 数据源
        opList: new historyModel(10),  // 历史记录列表
		canvas: null,	// 画布
		cxt: null,		// 画布接口
		clipMask: null,	// 蒙版画布
		maskCxt: null,	// 蒙版画布接口
		clipFrame: null,// 截图框
		originImgInfo: null,	// 初始图象信息
		edgeW: null,	// 画布可以延伸的最大宽度
		edgeH: null,	// 画布可以延伸的最大高度
		showLowerSide: true	// 显示选取/保存图片栏
    },
	mutations: { // commit 修改数据
		storeInterfaces (state, p) {
			// 将常用接口设为全局变量
			state.canvas = p.canvas;
			state.cxt = state.canvas.getContext('2d');
			state.clipMask = p.clipMask;
			state.maskCxt = state.clipMask.getContext('2d');
			state.clipFrame = p.clipFrame;
		},
		setEdge (state, p) {
			// 将边界数据设为全局变量
			state.edgeH = p.edgeH;
			state.edgeW = p.edgeW;
		},
		storeOriginImgInfo (state) {
			// 初始化，重置历史记录，将原图数据保存
			state.opList.clear()
			state.originImgInfo = state.cxt.getImageData(0, 0, state.canvas.width, state.canvas.height)
			state.opList.push({ data: state.originImgInfo, name: "原图" });
		},
		setToOrigin (state) {
			// 将画布恢复为原图状态
			state.cxt.clearRect(0, 0, state.canvas.width, state.canvas.height)
			state.canvas.width = state.originImgInfo.width
			state.canvas.height = state.originImgInfo.height
			state.cxt.putImageData(state.originImgInfo, 0, 0)
		},
	}
})

export default store;