class historyModel {
	constructor(capacity) {
		this.items = [];
		this.curPtr = -1;
		this.capacity = capacity;
	}

	// 获取指针位置
	getCurPtr() {
		return this.curPtr;
	}

	// 获取当前元素并把指针向前移
	getNowAndForward() {
		if (this.curPtr < 0) return;
		this.curPtr--;
		return this.items[this.curPtr];
	}

	// 获取当前元素并把指针向后移
	getNowAndBackward() {
		if (this.curPtr < 0) return;
		this.curPtr++;
		return this.items[this.curPtr];
	}

	// 获取i对应的元素并把指针指向它
	getAndSetNow(i) {
		if (i < 0 || i > this.size()) return;
		this.curPtr = i;
		return this.items[this.curPtr];
	}

	// 把指针后面的全清除
	removeBackPart() {
		if (this.curPtr == -1) return;
		this.items.splice(this.curPtr + 1);
		return;
	}
	
	// 新记录入列
	push(e) {
		if (e == undefined) {
			console.log('push undefined.');
			return;
		}
		if (this.items.length == this.capacity) {
			this.items.shift();
			console.log('超过容量，挤掉底部元素');
			this.curPtr--;
		}
		this.items.push(e);
		this.curPtr++;
		return;
	}
	
	// 获取当前列表中记录条数
	size() {
		return this.items.length;
	}
	
	// 清空列表
	clear() {
		this.items.splice(0, this.items.length);
		this.curPtr = -1;
		return;
	}
}

export {
	historyModel
}