<template>
	<v-container fluid>
		<!-- 功能抽屉 -->
		<v-list>
			<v-list-group v-for="item in items" :key="item.title" :prepend-icon="item.icon" no-action>
				<template v-slot:activator>
					<v-list-item-content>
						<v-list-item-title v-text="item.title"></v-list-item-title>
					</v-list-item-content>
				</template>

				<v-list-item
					v-for="subItem in item.items"
					:key="subItem.title"
					@click="subItem.action(subItem.funcId)"
				>
					<v-list-item-content>
						<v-list-item-title v-text="subItem.title"></v-list-item-title>
					</v-list-item-content>
				</v-list-item>
			</v-list-group>
		</v-list>
	</v-container>
</template>

<script>
import { parsing } from "../js/operator";
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
	name: "leftSidebar",
	computed: {
	// ========================== 全局变量 ==========================
	...mapState({
		// root
		opList: state => state.opList,
		canvas: state => state.canvas,
		cxt: state => state.cxt,
		
		// 裁剪相关
		clipState: state => state.clip.clipState,
		
		// 缩放相关
		scaleState: state => state.scale.scaleState,
		scaleRatio: state => state.scale.scaleRatio,
		
		// 旋转相关
		rotateState: state => state.rotate.rotateState,
	}),
  },
  data() {
    return {
      // ========================== 功能总览 ==========================
      items: [
        {
          icon: "mdi-pencil",
          title: "基础编辑",
          items: [
            { title: "水平翻转", action: this.basicOP, funcId: 3},
            { title: "垂直翻转", action: this.basicOP, funcId: 4},
			{ title: "裁剪", action: this.clipping, funcId: 7},
			{ title: "缩放", action: this.scaling, funcId: 8},
			{ title: "旋转", action: this.rotating, funcId: 9},
          ]
        },
        {
          icon: "mdi-pencil",
          title: "风格编辑",
          items: [
            { title: "反色", action: this.basicOP, funcId: 0},
            { title: "去色", action: this.basicOP, funcId: 1},
            { title: "浮雕", action: this.basicOP, funcId: 2},
			{ title: "黑白", action: this.basicOP, funcId: 5},
			{ title: "虚化", action: this.basicOP, funcId: 6},
          ]
        }
      ],
	
      // 需要检查的，可能与当前操作发生冲突的状态列表
      opCheckList: [
        {'op': this.getClipState, 'name': '裁剪', 'todo': this.endClip, 'param': 0},
        {'op': this.getScaleState, 'name': '缩放', 'todo': this.endScaling, 'param': 0},
        {'op': this.getRotateState, 'name': '旋转', 'todo': this.endRotating, 'param': 0}
      ]
    };
  },
  methods: {
	// ========================== 全局方法 ==========================
	...mapGetters('clip', [ 'getClipState']),
	...mapGetters('scale', [ 'getScaleState']),
	...mapGetters('rotate', [ 'getRotateState']),

	...mapActions('clip', [
		'initClip', 'endClip',
	]),
	...mapActions('scale', [
		'initScaling', 'endScaling',
	]),
	...mapActions('rotate', [
		'initRotation', 'endRotating'
	]),
	// ========================== 特殊处理 ==========================
	preCheck() {
		for(var j = 0,len = this.opCheckList.length; j < len; j++){
			var e = this.opCheckList[j];
			if (e.op()) {
				var msg = e.name + "结果尚未保存，是否放弃？";
				if (confirm(msg)) e.todo(e.param);
				return false;
			}
		}
		return true;
	},
	// ========================== 功能实现 ==========================
    // 直接操作图片像素的功能，操作过程中无需与用户交互
	basicOP(n) {
		if(!this.preCheck()) return;	// 冲突检测
		this.opList.removeBackPart();
		this.opList.push(parsing(this.canvas, n));
		console.log("opList: ", this.opList);
    },
	// 操作过程中需要用户参与的功能
	clipping(n) {	// 裁剪
		if(!this.preCheck()) return;
		this.initClip(n);
	},
	scaling(n) {	// 缩放
		if(!this.preCheck()) return;
		this.initScaling(n);
	},
	rotating(n) {	// 旋转
		if(!this.preCheck()) return;
		this.initRotation(n);
	}
  }
};
</script>

