import { weightingFactor, createMartixArray, weightingFactorValue } from "./maths.js"

function resetWithImgData(canvas, imgData) {
	/* 将画布重设为传入的图片状态 */
	console.log('重设');
	var cxt = canvas.getContext("2d");
	
	// 清空当前画布
	cxt.clearRect(0, 0, canvas.width, canvas.height);
	// 重设
	canvas.width = imgData.width;
	canvas.height = imgData.height;
	cxt.putImageData(imgData, 0, 0);
	return;
}

function parsing(canvas, funcNum) {
	/* 根据操作编号对图片做相应的编辑，并返回操作结果 */
	var cxt = canvas.getContext("2d");
	var imageData = cxt.getImageData(0, 0, canvas.width, canvas.height);
	var funcName = new String
	switch (funcNum) {
		case 0:
			revColor(cxt, imageData);
			funcName = "反色"
			break;
		case 1:
			fade(cxt, imageData);
			funcName = "去色"
			break;
		case 2:
			relievo(cxt, imageData);
			funcName = "浮雕"
			break;
		case 3:
			flipHorizontal(cxt, imageData)
			funcName = "水平翻转"
			break;
		case 4:
			flipVertical(cxt, imageData)
			funcName = "垂直翻转"
			break;
		case 5:
			blackWhite(cxt, imageData)
			funcName = "黑白"
			break;
		case 6:
			blur(cxt, imageData)
			funcName = "虚化"
			break;
		case 7:
			funcName = "裁剪"
			break;
		case 8:
			funcName = "缩放"
			break;
		case 9:
			funcName = "旋转"
			break;
		default:
			console.log('未定义的功能');
			break;
	}
	return { data: imageData, name: funcName }; // 返回变化后的数据
}

function revColor(cxt, imageData) {
	console.log('反色');
	var imageData_length = imageData.data.length / 4;
	for (var i = 0; i < imageData_length; i++) {
		imageData.data[i * 4] = 255 - imageData.data[i * 4];
		imageData.data[i * 4 + 1] = 255 - imageData.data[i * 4 + 1];
		imageData.data[i * 4 + 2] = 255 - imageData.data[i * 4 + 2];
	}
	cxt.putImageData(imageData, 0, 0);
}

function fade(cxt, imageData) {
	console.log('去色');
	var imageData_length = imageData.data.length / 4;
	for (var i = 0; i < imageData_length; i++) {
		var red = imageData.data[i * 4];
		var green = imageData.data[i * 4 + 1];
		var blue = imageData.data[i * 4 + 2];
		var gray = 0.3 * red + 0.59 * green + 0.11 * blue;
		imageData.data[i * 4] = gray;
		imageData.data[i * 4 + 1] = gray;
		imageData.data[i * 4 + 2] = gray;
	}
	cxt.putImageData(imageData, 0, 0);
}

function relievo(cxt, imageData) {
	console.log('浮雕');
	var lastR = imageData.data[0], lastG = imageData.data[1], lastB = imageData.data[2];
	var imageData_length = imageData.data.length / 4;
	for (var i = 0; i < imageData_length; i++) {
		var red = imageData.data[i * 4];
		var green = imageData.data[i * 4 + 1];
		var blue = imageData.data[i * 4 + 2];
		imageData.data[i * 4] += (128 - lastR);
		imageData.data[i * 4 + 1] += (128 - lastG);
		imageData.data[i * 4 + 2] += (128 - lastB);
		lastR = red;
		lastG = green;
		lastB = blue;
	}
	cxt.putImageData(imageData, 0, 0);
}

function flipHorizontal(cxt, imageData) {
	console.log('水平翻转')
	
	// 1.获取图像信息
	const w = imageData.width
	const h = imageData.height
	let middleAxle = (w * 4) / 2;

	// 2.遍历每一行作为外循环
	for (let curRow = 0; curRow < h; curRow++) {
		// 每行开始的通道位置
		let aisleStart = curRow * w * 4,
			// 每行结束的通道位置
			aisleEnd = (curRow + 1) * w * 4 - 4;
		// 每一行中轴所在的位置
		let curMiddleAxle = aisleEnd - middleAxle;

		// 3.遍历当前行的列作为内循环,把列的左边像素按照轴对称和右边的像素互换
		for (; aisleStart <= curMiddleAxle; aisleStart += 4, aisleEnd -= 4) {
			// 临时存放
			let tr = imageData.data[aisleStart],
				tg = imageData.data[aisleStart + 1],
				tb = imageData.data[aisleStart + 2],
				ta = imageData.data[aisleStart + 3];

			imageData.data[aisleStart] = imageData.data[aisleEnd];
			imageData.data[aisleStart + 1] = imageData.data[aisleEnd + 1];
			imageData.data[aisleStart + 2] = imageData.data[aisleEnd + 2];
			imageData.data[aisleStart + 3] = imageData.data[aisleEnd + 3];

			imageData.data[aisleEnd] = tr;
			imageData.data[aisleEnd + 1] = tg;
			imageData.data[aisleEnd + 2] = tb;
			imageData.data[aisleEnd + 3] = ta;
		}
	}

	cxt.putImageData(imageData, 0, 0);
}


function flipVertical(cxt, imageData) {
	console.log('垂直翻转')
	
	// 1.获取图像信息
	const w = imageData.width
	const h = imageData.height
	let middleAxle = Math.floor(h / 2),
		rowAisles = w * 4;

	// 2.遍历总行数一半的每一行作为外循环(向下取整)
	for (var curRow = 0; curRow < middleAxle; curRow++) {
		// 开始的通道位置
		let aisleStart = curRow * rowAisles,
			// 镜像对称的开始位置
			mirrorStart = (h - curRow - 1) * rowAisles;

		// 3.遍历当前行的列作为内循环,把列的每个通道按照水平轴对称和镜像里的通道互换
		for (
			;
			aisleStart < rowAisles * (curRow + 1);
			aisleStart += 4, mirrorStart += 4
		) {
			var tr = imageData.data[aisleStart],
				tg = imageData.data[aisleStart + 1],
				tb = imageData.data[aisleStart + 2],
				ta = imageData.data[aisleStart + 3];

			imageData.data[aisleStart] = imageData.data[mirrorStart];
			imageData.data[aisleStart + 1] = imageData.data[mirrorStart + 1];
			imageData.data[aisleStart + 2] = imageData.data[mirrorStart + 2];
			imageData.data[aisleStart + 3] = imageData.data[mirrorStart + 3];

			imageData.data[mirrorStart] = tr;
			imageData.data[mirrorStart + 1] = tg;
			imageData.data[mirrorStart + 2] = tb;
			imageData.data[mirrorStart + 3] = ta;
		}
	}

	// 4.把处理后的像素信息放回画布
	cxt.putImageData(imageData, 0, 0);
}

function blackWhite(cxt, imageData) {
	console.log('黑白')
	
	// 1.获取图像信息
	let imageData_length = imageData.data.length;

	// 2.遍历每一个像素
	for (let i = 0; i < imageData_length; i += 4) {
		// 3.将每个像素的所有通道的值按权平均,和中性灰的色值进行对比
		// 当前的r,g,b色值
		let r = imageData.data[i],
			g = imageData.data[i + 1],
			b = imageData.data[i + 2];

		// 计算结果值
		let val = parseInt(r * 0.3 + g * 0.59 + b * 0.11) >= 128 ? 255 : 0;
		// 黑白后的r,g,b色值
		imageData.data[i] = val;
		imageData.data[i + 1] = val;
		imageData.data[i + 2] = val;
	}

	// 4.把处理后的像素信息放回画布
	cxt.putImageData(imageData, 0, 0);
}

function blur(cxt, imageData, R = 1) {
	console.log('虚化')
	
	// 1.获取图像信息
	const w = imageData.width
	const h = imageData.height
	// 图像的通道集合
	var pxInfo = imageData.data;

	// 2.根据模糊半径求得周边每个点的权重
	var weightingfactor = weightingFactor(createMartixArray(R));

	// 3.遍历每一行
	for (let curRow = 0; curRow < h; curRow++) {
		// 4.在当前行内遍历列的每个点
		for (let curCol = 0; curCol < w; curCol++) {
			// 5.在当前点遍历周边所有像素点,以通道作为新的集合
			var calDataR = [], // 周围红通道的集合
				calDataG = [], // 周围绿通道的集合
				calDataB = [], // 周围蓝通道的集合
				calDataA = []; // 周围Alpha通道的集合
			// 获取周边像素信息的行
			for (let i = -Math.abs(R); i <= Math.abs(R); i++) {
				if (curRow + i < 0 || curRow + i >= h) {
					// 若超出行边界，跳过
					continue;
				}
				// 获取周边像素所有通道的集合
				for (let j = -Math.abs(R); j <= Math.abs(R); j++) {
					if (curCol + j < 0 || curCol + j >= w) {
						// 若超出列边界，跳过
						continue;
					}
					var index = ((curRow + i) * w + curCol + j) * 4;
					calDataR.push(pxInfo[index]);
					calDataG.push(pxInfo[index + 1]);
					calDataB.push(pxInfo[index + 2]);
					calDataA.push(pxInfo[index + 3]);
				}
			}

			// 6.对每个通道进行权重值的计算
			imageData.data[(curRow * w + curCol) * 4] = weightingFactorValue(
				weightingfactor,
				calDataR
			);
			imageData.data[(curRow * w + curCol) * 4 + 1] = weightingFactorValue(
				weightingfactor,
				calDataG
			);
			imageData.data[(curRow * w + curCol) * 4 + 2] = weightingFactorValue(
				weightingfactor,
				calDataB
			);
			imageData.data[(curRow * w + curCol) * 4 + 3] = weightingFactorValue(
				weightingfactor,
				calDataA
			);
		}
	}

	// 7.把处理后的像素信息放回画布
	cxt.putImageData(imageData, 0, 0);
}


export {
	parsing,
	resetWithImgData
}